import './App.css';
import { render } from '@testing-library/react';
import React, {Component} from 'react';
import {evaluate, expression, isInteger} from 'mathjs';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      calcDisplay:'0',
    }
    
    // bind here
    this.handleClick = this.handleClick.bind(this);
    this.handleClear = this.handleClear.bind(this);
    this.handleEnter = this.handleEnter.bind(this);
  }





handleClick(e){
  if (this.state.calcDisplay === '0'){
    if (e.target.className === 'op' && e.target.value !== '-'){
      return;
    } else{
      this.setState({
        calcDisplay: e.target.value
      });
    }
  } else {
    let lastEntry = this.state.calcDisplay.slice(-1);
    // Overwrites the last operation when trying to input two operations back to back.
    // Can only input a "-" after an operation
    if ((lastEntry === '/' || lastEntry === '+' || lastEntry == 'x' || lastEntry == '-') && (e.target.className == 'op')){
      
      if(e.target.value === '-'){
        this.setState({
          calcDisplay: this.state.calcDisplay + e.target.value
        });
      } else{
        this.setState({
          calcDisplay: this.state.calcDisplay.slice(0,-1) + e.target.value
        })
      }
    } else if (lastEntry === '-' && e.target.className == 'op'){
      this.setState({
        calcDisplay: this.state.calcDisplay.slice(0,-1) + e.target.value
      })
    } else{
    this.setState({
      calcDisplay: this.state.calcDisplay + e.target.value
    });
    }
  }
  return;
}

handleClear(e){
  if (e.target.value === "clear") {
    this.setState({
      calcDisplay: '0',
    });

  // Single back space, or reset to 0 if length is 1
  } else if(e.target.value === "back" && this.state.calcDisplay !== 0){
    if (this.state.calcDisplay.length === 1){
      this.setState({
        calcDisplay: '0'
      })
    } else{
      this.setState({
        calcDisplay: this.state.calcDisplay.slice(0, -1) 
      })
    }

  }
}

handleEnter(e){
  if (this.state.calcDisplay.length <= 1){
    return;
  }
  // Check if last entry is not a number
  let lastEntry = this.state.calcDisplay.slice(-1);
  if(!(Number.isInteger(parseInt(lastEntry)))){
    return;
  }
  
  let result = String(evaluate(this.state.calcDisplay.replace('x', '*')));
  this.setState({
    calcDisplay: result,
  })
  }


render() {
  return (
    <div id="calculator">
      <div id="display">
        <div id="calcField">{this.state.calcDisplay}</div>

        <div className="row">
          <button value='clear' id="clear" onClick={this.handleClear}>AC</button>
          <button value='back' id="backspace" onClick={this.handleClear}></button>
          <button className='op' value='+' id="add" onClick={this.handleClick}>+</button>


        </div>
        <div className="row">
          <button value='7' id="seven" onClick={this.handleClick}>7</button>
          <button value='8' id="eight" onClick={this.handleClick}>8</button>
          <button value='9' id="nine" onClick={this.handleClick}>9</button>
          <button className='op' value='-' id="subtract" onClick={this.handleClick}>-</button>



        </div>
        <div className="row">
          <button value='4' id="four" onClick={this.handleClick}>4</button>
          <button value='5' id="five" onClick={this.handleClick}>5</button>
          <button value='6' id="six" onClick={this.handleClick}>6</button>
          <button className='op' value='/' id="divide" onClick={this.handleClick}>/</button>

        </div>
        <div className="row">
          <button value='1' id="one" onClick={this.handleClick}>1</button>
          <button value='2' id="two" onClick={this.handleClick}>2</button>
          <button value='3' id="three" onClick={this.handleClick}>3</button>
          <button className='op' value='x' id="multiply" onClick={this.handleClick}>x</button>



        </div>
        <div className="row" >
          <button value='0' id="zero" onClick={this.handleClick}>0</button>
          <button value='.' id="decimal" onClick={this.handleClick}>.</button>
          <button value='=' id="enter" onClick={this.handleEnter}>=</button>

        </div>

      </div>

    </div>
  )
}
}
export default App;
